import '../../styles/buttonHome.css'

export default function ButtonHome(prop) {

  var button = prop.names;
  var linkFilter = prop.linkFilter;
  
 // console.log(linkFilter)

  if(linkFilter !== undefined){ // vai identificar se o parmetro responsvel por filter um boto que no queremos foi passado
    linkFilter.map((linkRemove)=>{
      button = button.filter((array)=>{// filter o item do array que corresponde ao boto que no queremos na pagina
        if( array.name !== linkRemove){ //verifica se o array  o que no queremos remover
          return array
        }else{ // vai cair que se o for achado o link do boto que no queremos na pgina
          return'' //nada ser passado para o novo array
        }
      })  
    })
}




function modLinkOpen(button){

  if(button.slice(0,1) === "/"){
    return true
  }else{
    //console.log("")
    return false
  }
}

const showButtons = button.map((button,i) =>{ //vai renderizar o array dos botes

  return(
   
      <a 
      key={i}
      href={`${button.link}`}
      target={ "_self" } 
      rel="noreferrer"
      className="link-button">
      <div className="card-button">
        <img
          
          src={`/icons/${button.img}.svg`}
          alt="cones"
          className="svg-button"
          />
        <p className="text-card-button">
          {button.name}
        </p>
      </div>
    </a>

  )
  });
  return (
    <div className="App">
      <div className="container-card">
            {showButtons}
      </div>
    </div>
  );
}
  